import React, {Component} from 'react'
import './style.css'
import lockImg from './lock.png';


export default class UserAvatar extends Component{
    state = {
        choosed: false
    }

    render(){
        const user = this.props.user
        let baseClasses = ['userContainer']
        if(this.state.choosed) baseClasses.push('choosed')
        const borderColor = user.sex === 2? 'blue' : 'red'
        const lockIcon = <img src={lockImg} className='lock' alt='lock'></img>
        return (
            <div className={baseClasses.join(' ')}>
                <img onClick={this.imgClick} style = {{borderColor: borderColor}} className='avatar' title={user.id} src={user.photo} alt=""></img>
                {user.private && lockIcon}
                <a className='name' href={user.url} target='_blank' rel="noopener noreferrer">
                    <div className='d-flex justify-content-center'>{user.first_name}</div>
                    <div className='d-flex justify-content-center'>{user.last_name}</div>
                </a>
            </div>
        )
    }

    imgClick = () => {
        this.setState({
            choosed: !this.state.choosed
        })
    }
}   
